
import { PropType, computed, defineComponent, ref } from "vue";
import { LanguageData } from "@/types/LanguageData";
import { Objects } from "@/types/Objects";
import formatterOfNumber from "@/utils/formatterOfNumber";
import compareAlphabetically from "@/utils/compareAlphabetically";
import compareByNumber from "@/utils/compareByNumber";
import generateColors from "@/utils/generateColors";

import { Pie } from "vue-chartjs";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { useVueFuse } from "vue-fuse";
import { CurrenciesObject } from "@/types/CurrenciesObject";
ChartJS.register(ArcElement, Tooltip, Legend);

export default defineComponent({
  name: "ModalWindowStatistics",
  components: { Pie },
  props: {
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    currenciesObject: {
      type: Object as PropType<CurrenciesObject>,
      required: true,
    },
  },
  setup(props) {
    const viewObjects = ref(false);
    const averageObjects = computed(() => {
      const grouped = props.objects.reduce((acc: any, item) => {
        const currency = item.currency;
        const layout = item.layout;
        if (!acc[currency]) {
          acc[currency] = {
            sum: {},
            count: {},
            averages: [],
          };
        }
        if (!acc[currency].sum[layout]) {
          acc[currency].sum[layout] = 0;
        }
        if (!acc[currency].count[layout]) {
          acc[currency].count[layout] = 0;
        }
        acc[currency].sum[layout] += item.price;
        acc[currency].count[layout]++;
        return acc;
      }, {});

      const averageObjects = Object.entries(grouped).map(([currency, data]) => {
        const sum = (data as any).sum;
        const count = (data as any).count;
        const averages = (data as any).averages;
        const layouts = Object.keys(sum);
        layouts.forEach((layout) => {
          const avg = sum[layout] / count[layout];
          averages.push({
            layout,
            average: Math.round(avg),
          });
        });
        return {
          currency,
          averages: averages.sort(compareAlphabetically("layout")),
        };
      });

      return averageObjects;
    });

    const nonRepeatingObjects = computed(() => {
      return [
        ...new Map(props.objects.map((item) => [item.name, item])).values(),
      ];
    });

    const {
      search,
      results: resultsSearch,
      noResults: noResultsSearch,
    } = useVueFuse(nonRepeatingObjects, {
      keys: ["name"],
      threshold: 0.3,
      minMatchCharLength: 1,
    });

    const distrcitsStatisticData = computed(() => {
      const allDistricts = nonRepeatingObjects.value.map(
        (item) => item.district
      );

      const districtCounts: { [key: string]: string | number } = {};

      allDistricts.forEach((district) => {
        districtCounts[district] = districtCounts[district]
          ? (districtCounts[district] as number) + 1
          : 1;
      });

      const districtPercentages = Object.keys(districtCounts).map(
        (district) => ({
          district,
          percentage:
            ((districtCounts[district] as number) / allDistricts.length) * 100,
        })
      );

      let totalPercentage = 0;

      if (districtPercentages.length > 0) {
        districtPercentages.forEach((item) => {
          item!.percentage = Number(item.percentage.toFixed(2));
          totalPercentage += item.percentage;
        });

        if (totalPercentage !== 100) {
          const difference = 100 - totalPercentage;
          const maxIndex = districtPercentages.reduce(
            (maxIndex, item, index, array) =>
              item.percentage > array[maxIndex].percentage ? index : maxIndex,
            0
          );
          districtPercentages[maxIndex].percentage += difference;
        }
      }

      const labels = [
        ...new Set(nonRepeatingObjects.value.map((item) => item.district)),
      ];
      const data = labels.map(
        (label) =>
          nonRepeatingObjects.value.filter((item) => item.district === label)
            .length
      );
      return {
        districtPercentages: districtPercentages
          .sort(compareByNumber("percentage"))
          .reverse(),
        chartData: {
          labels,
          datasets: [
            {
              backgroundColor: generateColors(labels.length),
              data,
            },
          ],
        },
      };
    });

    return {
      viewObjects,
      search,
      resultsSearch,
      noResultsSearch,
      nonRepeatingObjects,
      averageObjects,
      formatterOfNumber,
      distrcitsStatisticData,
    };
  },
});
