
import { Object } from "@/types/Object";
import { PropType, defineComponent, ref } from "vue";
import FieldTitleAndValue from "./FieldTitleAndValue.vue";
import { LanguageData } from "@/types/LanguageData";
import getDataFromUser from "@/utils/getDataFromUser";
import axios from "axios";
import { toast } from "vue3-toastify";

export default defineComponent({
  name: "AccordionActionChange",
  emits: ["setEditParams", "updateEditParams"],
  props: {
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
    edit: {
      type: Object as PropType<Object>,
      required: true,
    },
    object: {
      type: Object as PropType<Object>,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
  },
  setup(props, { emit }) {
    const disableButton = ref(false);
    const updateEdit = (name: keyof Object, value: any) => {
      emit("updateEditParams", { ...props.edit, [name]: value });
    };
    const sendEditObject = () => {
      const URL = window.location.origin + "/#/changes";
      let userData = getDataFromUser();
      if (userData === false) {
        toast.error(
          props.languageData.TRANSLATION.ERROR_HAS_OCCURED[
            props.languageData.lang
          ],
          {
            autoClose: 3000,
            position: toast.POSITION.BOTTOM_RIGHT,
          }
        );
        return;
      }
      userData = userData as { name: string; token: string };
      disableButton.value = true;
      const params = {
        from: encodeURIComponent(userData.name),
        type: "edit",
        obj_1: encodeURIComponent(
          JSON.stringify({
            ...props.object,
            link: `https://drive.google.com/drive/folders/${props.object.link}`,
            coordinates: `[ ${props.edit.coordinates[0]},${props.edit.coordinates[1]} ]`,
          })
        ),
        obj_2: encodeURIComponent(
          JSON.stringify({
            ...props.edit,
            coordinates: `[ ${props.edit.coordinates} ]`,
          })
        ),
      };
      const text = `${URL}?from=${params.from}&type=${params.type}&obj_1=${params.obj_1}&obj_2=${params.obj_2}`;
      axios
        .get(
          `https://api.telegram.org/bot${
            userData.token
          }/sendMessage?chat_id=1049752057&text=${encodeURIComponent(text)}`
        )
        .then((response) => {
          if (response.data.ok) {
            toast.success(
              props.languageData.TRANSLATION.SUCCESS[props.languageData.lang],
              {
                autoClose: 3000,
                position: toast.POSITION.BOTTOM_RIGHT,
              }
            );
            disableButton.value = false;
          }
        })
        .catch((err) => {
          console.error(err);
          toast.error(
            props.languageData.TRANSLATION.ERROR_HAS_OCCURED[
              props.languageData.lang
            ],
            {
              autoClose: 3000,
              position: toast.POSITION.BOTTOM_RIGHT,
            }
          );
          disableButton.value = false;
        });
    };
    return {
      disableButton,
      updateEdit,
      sendEditObject,
    };
  },
  components: { FieldTitleAndValue },
});
