
import { PropType, defineComponent } from "vue";
import { Objects } from "@/types/Objects";
import FilterDistricts from "./FilterDistricts.vue";
import FilterPrice from "./FilterPrice.vue";
import FilterLayouts from "./FilterLayouts.vue";
import FilterDateOfCompletion from "./FilterDateOfCompletion.vue";
import { DataOfFiltres } from "@/types/DataOfFiltres";
import { LanguageData } from "@/types/LanguageData";

export default defineComponent({
  name: "FilterForm",
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    dataOfFiltres: {
      type: Object as PropType<DataOfFiltres>,
      required: true,
    },
    languageData: {
      type: Object as PropType<LanguageData>,
      required: true,
    },
  },
  components: {
    FilterDistricts,
    FilterPrice,
    FilterLayouts,
    FilterDateOfCompletion,
  },
  emits: ["update:dataOfFiltres", "findObjects"],
  setup(props, { emit }) {
    const districts = [
      ...new Set(props.objects.map((item) => item.district)),
    ].sort();
    const layouts = [
      ...new Set(props.objects.map((item) => item.layout)),
    ].sort();
    const currencies = [...new Set(props.objects.map((item) => item.currency))];

    const updateDataOfFiltres = (
      key: keyof DataOfFiltres,
      value: string | number
    ) => {
      emit("update:dataOfFiltres", {
        ...props.dataOfFiltres,
        [key]: value,
      });
    };

    return {
      districts,
      layouts,
      currencies,
      updateDataOfFiltres,
    };
  },
});
